<template>
<v-card
    class="mx-auto my-12 mb-2"
    max-width="374"

  >
  <div class="submit-form mt-4 mx-auto">
    <p class="headline">{{ action.title }} расписания</p>

    <div v-if="!submitted">
      <v-form ref="form" v-model="valid" lazy-validation>
        <v-text-field
          v-model="timeTable.subject"
          :rules="subjectRules"
          :counter="50"
          label="Название"
          required
        ></v-text-field>

        <v-text-field
            v-model="timeTable.cabinet"
            :rules="cabinetRules"
            :counter="4"
            label="Кабинет"
            required
        ></v-text-field>

        <v-select
          :items="isEven"
          v-model="timeTable.isEven"
          :rules="isEvenRules"
          label="Четная ли"
        ></v-select>

        <v-select
          :items="weekDay"
          v-model="timeTable.weekDay"
          :rules="weekDayRules"
          label="День недели"
        ></v-select>

        <!-- Время начала пары -->
        <v-dialog
          ref="menu"
          v-model="timePicker1"
          :return-value.sync="timeTable.timeStart"
          persistent
          width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="timeTable.timeStart"
              label="Время начала пары"
              prepend-icon="mdi-clock-time-four-outline"
              readonly
              :rules="timeStartRules"
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-time-picker
            v-if="timePicker1"
            v-model="timeTable.timeStart"
            format="24hr"
            full-width
          >
            <v-spacer></v-spacer>
            <v-btn text color="primary" @click="timePicker1 = false">
              Cancel
            </v-btn>
            <v-btn
              text
              color="primary"
              @click="$refs.menu.save(timeTable.timeStart)"
            >
              OK
            </v-btn>
          </v-time-picker>
        </v-dialog>

        <!-- Время окончания -->
        <v-dialog
          ref="dialog"
          v-model="timePicker2"
          :return-value.sync="timeTable.timeEnd"
          persistent
          width="290px"
        >
          <template v-slot:activator="{ on, attrs }">
            <v-text-field
              v-model="timeTable.timeEnd"
              label="Время окончания пары"
              prepend-icon="mdi-clock-time-four-outline"
              :rules="timeEndRules"
              readonly
              v-bind="attrs"
              v-on="on"
            ></v-text-field>
          </template>
          <v-time-picker
            v-if="timePicker2"
            v-model="timeTable.timeEnd"
            format="24hr"
            full-width
          >
            <v-spacer></v-spacer>
            <v-btn text color="primary" @click="timePicker2 = false">
              Cancel
            </v-btn>
            <v-btn
              text
              color="primary"
              @click="$refs.dialog.save(timeTable.timeEnd)"
            >
              OK
            </v-btn>
          </v-time-picker>
        </v-dialog>
        <v-text-field
          v-model="timeTable.teacher"
          :rules="teacherRules"
          label="Преподаватель"
          :counter="50"
          required
        ></v-text-field>
      </v-form>
      <v-btn
        :disabled="!valid"
        color="success"
        class="w-100 addBtn"
        @click="saveData"
      >
        {{ action.button }}
      </v-btn>
    </div>

    <div v-else>
      <v-card class="mx-auto">
        <v-card-title> Submitted successfully! </v-card-title>

        <v-card-subtitle>
          Click the button to add new Tutorial.
        </v-card-subtitle>

        <v-card-actions>
          <v-btn color="success">Add</v-btn>
        </v-card-actions>
      </v-card>
    </div>

    <v-snackbar v-model="snackbar" rounded="pill" :timeout="timeout">
      {{ message }}
    </v-snackbar>
  </div>
</v-card>
</template>

<script>
import TimeTable from "../models/timeTable";
// eslint-disable-next-line no-unused-vars
import authHeader from '../services/auth-header';
import axios from "axios";
export default {
  name: "add-timetable",
  data() {
    return {
      isEven: ["Да", "Нет"],
      weekDay: ["Пн", "Вт", "Ср", "Чт", "Пт"],
      timePicker1: false,
      timePicker2: false,
      timeTable: new TimeTable(null, null, null, null, null, null, null,null),
      submitted: false,
      message: null,
      valid: true,
      subjectRules: [
        (v) => !!v || "Предмет не указан",
        (v) => (v && v.length <= 50) || "Длинна не должна превышать 50 букв",
        (v) =>
          (v && v.length >= 2) || "Длинна названия должна быть не менее 2 букв",
      ],
      teacherRules: [
        (v) => !!v || "Преподавател не указан",
        (v) => (v && v.length <= 50) || "Длинна не должна превышать 50 букв",
        (v) =>
          (v && v.length >= 2) || "Длинна названия должна быть не менее 2 букв",
      ],
      isEvenRules: [(v) => !!v || "Четность недели не указана"],
      timeStartRules: [(v) => !!v || "Время начал а пары не указано"],
      timeEndRules: [(v) => !!v || "Время окончания пары не указано"],
      weekDayRules: [(v) => !!v || "День недели не задан"],

      cabinetRules: [(v) => !!v || "Кабинет не указан",
        (v) => (v && v.length <= 4) || "Длинна не должна превышать 4 цифы",
        (v) =>
            (v && v.length >= 1) || "Длинна дожна быть не менее 1 цифры",
      ],

      snackbar: false,
      timeout: 3000,

      action: {},
      isEdite: false,
    };
  },
  methods: {
    saveData() {
      this.$refs.form.validate();
      if (
        this.timeTable.isEven &&
        this.timeTable.weekDay &&
        this.timeTable.timeStart &&
        this.timeTable.timeEnd &&
        this.timeTable.subject &&
        this.timeTable.subject.length > 1 &&
        this.timeTable.subject.length < 50 &&
        this.timeTable.teacher &&
        this.timeTable.teacher.length > 1 &&
        this.timeTable.teacher.length < 50&&
        this.timeTable.cabinet &&
        this.timeTable.cabinet.length >= 1 &&
        this.timeTable.cabinet.length <= 4
      ) {
        this.$refs.form.resetValidation();
        // изменяем значение на bool
        if (this.timeTable.isEven == "Да") this.timeTable.isEven = 1;
        else this.timeTable.isEven = 0;

        if (this.isEdite) this.saveEdits(this.timeTable, this.timeTable.id);
        else this.saveDatas(this.timeTable);
      }
    },
    saveDatas(data) {
      axios
        .post("api/timetable", {
          isEven: data.isEven,
          weekDay: data.weekDay,
          timeStart: data.timeStart,
          timeEnd: data.timeEnd,
          subject: data.subject,
          teacher: data.teacher,
          cabinet: data.cabinet,
          token:authHeader()
        })
        .then((response) => {
          this.message = response.data.message;
          this.snackbar = true;
          //  setInterval(() => {
          this.$router.push({ name: "timetibleList" });
          // }, this.timeout);
        });
    },
    saveEdits(data, id) {
      axios
        .put("api/timetable/" + id + "?token=" + authHeader(), {
          isEven: data.isEven,
          weekDay: data.weekDay,
          timeStart: data.timeStart,
          timeEnd: data.timeEnd,
          subject: data.subject,
          teacher: data.teacher,
          cabinet: data.cabinet,
        })
        .then((response) => {
          this.message = response.data.message;
          this.snackbar = true;
          //   setInterval(() => {
          this.$router.push({ name: "timetibleList" });
          //   }, this.timeout);
        }).catch(error => console.log(error));
    },
    getData(id) {
      axios
        .get("api/timetable/" + id + "?token=" + authHeader())
        .then((response) => {
          this.timeTable = this.getDisplayData(response.data);
        })
        .catch((error) => console.log(error));
    },
    getDisplayData(data) {
      return {
        id: data.id,
        isEven: data.isEven ? "Да" : "Нет",
        weekDay: data.weekDay,
        timeStart: data.timeStart,
        timeEnd: data.timeEnd,
        subject: data.subject,
        teacher: data.teacher,
        cabinet: data.cabinet,

      };
    },
  },
  mounted() {
    if (this.$route.params.id) {
      this.getData(this.$route.params.id);
      this.action = {
        title: "Изменение",
        button: "Изменить",
      };
      this.isEdite = true;
    } else {
      this.action = {
        title: "Добавление",
        button: "Добавить",
      };
      this.isEdite = false;
    }
  },
};
</script>

<style>
.submit-form {
  max-width: 300px;
}
.addBtn{
  margin-bottom: -15px;
}
</style>